<template>
    <div>
        <VueCropper
                style=" height: 500px;"
                ref="cropper"
                enlarge="2"
                centerBox
                :info="true"
                autoCrop
                autoCropWidth="300"
                autoCropHeight="300"
                :img="option.img"
                :outputSize="option.size"
                :outputType="option.outputType">
        </VueCropper>
        <el-upload
                :show-file-list="false"
                :multiple="false"
                action=""
                :on-change="onChange"
                :before-upload="beforeUpload"
                :auto-upload="false"
                :http-request="onStartUpload"
                ref="upload">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
        </el-upload>
    </div>
</template>

<script>
    import {VueCropper} from 'vue-cropper';
    import {dataToFile} from '@/utils/files.js'
    import {uploadFile} from '@/api/file/file.js'
    import {getCookie} from '@/utils/cookie.js'
    import {megSuc,megErr} from '@/utils/alert.js'

    export default {
        name: "addBaner",
        components:{
            VueCropper
        },
        data() {
            return{
                option:{
                    img:'',
                    size:1,
                    outputType:'jpeg'
                }
            }
        },
        mounted() {

        },
        methods:{
            onChange(file) {
                let _this = this;
                if(file.raw) {
                    let reader = new FileReader();
                    reader.readAsDataURL(file.raw);
                    reader.onload = function (e) {
                        _this.option.img = this.result
                    };
                }
            },
            beforeUpload(file) {
                console.log(11)
            },
            onStartUpload() {
                console.log(222)
            },
            submitUpload() {
                this.$refs.cropper.getCropData((data) => {
                    let file = dataToFile(data, `${Date.now()}.jpg`);
                    uploadFile(file).then(res => {
                        if (res.status == 200) {
                            megSuc(res.data.message)
                        }
                    });
                })
            }

        }
    }
</script>

